


<template #default="scope">
  <div style="padding: 10px;">

    <!---->
    <div style="margin: 10px;padding: 0px">
      <el-button color="#3B455B" style="color: white" @click="add" v-if="user.role.roleKey === 'superuser'"></el-button>
      <el-button color="#3B455B" style="color: white" @click="add1" v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin' || user.role.roleKey === 'designer'"></el-button>
      <el-button color="#3B455B" style="color: white" @click="updateM" v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin' || user.role.roleKey === 'designer'"></el-button>
      <el-button color="#3B455B" style="color: white" @click="exportExcel"></el-button>
      <el-button color="#3B455B" style="color: white" @click="load"></el-button>
    </div>
    <!---->
    <div style="margin: 10px ; padding: 0px">
      <el-form :inline="true" :model="formInline" class="demo-form-inline" ref="resetformInline">
        <el-form-item label="">
          <el-input v-model="formInline.materialNo" placeholder="please input" clearable/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formInline.itemNo" placeholder="Please input"/>
        </el-form-item>
        <el-form-item label="" >
          <el-autocomplete
              v-model="userInputValue"
              :fetch-suggestions="queryUserSearchAsync"
              :popper-append-to-body="true"
              :placeholder="''"
              :trigger-on-focus="true"
              :value-key="'label'"
              @select="handleUserSelect"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formInline.documentType"
                     placeholder="Please select"
                     collapse-tags
                     collapse-tags-tooltip
                     multiple >
            <el-option label="" value="1"/>
            <el-option label="" value="2"/>
            <el-option label="" value="3"/>
            <el-option label="" value="4"/>
            <el-option label="" value="5"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
              v-model="createTimeDaterange"
              type="datetimerange"
              :shortcuts="shortcuts"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label=""
                      v-if="user.role.roleKey === 'superuser' || (user.role.roleKey === 'regular_user' && user.depart.parentId !== 8 && user.depart.parentId !== 9)">
          <el-date-picker
              v-model="publishTimeDaterange"
              type="datetimerange"
              :shortcuts="shortcuts"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
          >
          </el-date-picker>
        </el-form-item>


        <el-form-item label=""
                      v-if="user.role.roleKey === 'superuser' || (user.role.roleKey === 'regular_user' && user.depart.parentId !== 8 && user.depart.parentId !== 9)">
          <el-autocomplete
              v-model="formInline.superDepartInputValue"
              :fetch-suggestions="querySuperDepartSearchAsync"
              :popper-append-to-body="true"
              :placeholder="''"
              :trigger-on-focus="true"
              :value-key="'label'"
              @select="handleSuperDepartSelect"
          ></el-autocomplete>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" style="margin-left: 5px" :icon="Search" @click="load"></el-button>
          <el-button type="warning" style="margin-left: 5px" :icon="RefreshLeft" @click="reset"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div style="margin: 10px ; padding: 0px">
      <el-button type="primary" @click="handleDownLoadDocuments"></el-button>
      <el-button  color="#626aef"
                  @click="handleVerifyDocuments"
                 v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin'"
      ></el-button>
      <el-popconfirm title="?"
                     @confirm="handleDeleteDocuments"
                     v-if="user.role.roleKey === 'superuser'">
        <template #reference>
          <el-button type="danger" ></el-button>
        </template>
      </el-popconfirm>
    </div>
    <!---->

    <div style="height: 500px;">
      <el-scrollbar wrap-class="scrollbar-wrap">
        <el-table
          v-loading="loading"
          :data="tableData"
          style="width: 100%;font-size: 14px; height: 500px; font-family: Arial, sans-serif; font-weight: bold;"
          :header-cell-style="{'text-align':'center'}"
          :cell-style="{'text-align':'center'}"
          border
          class="table"
          :stripe="false"
          @selection-change="handleSelectionChange"
          @sort-change='sortChange'
      >
        <el-table-column type="selection" width="55"/>
        <el-table-column prop="id" label="ID"  />
        <el-table-column prop="itemNo" label="" width="100"/>
        <el-table-column prop="materialNo" label="" width="100"/>
        <el-table-column prop="documentName" label="" show-overflow-tooltip/>
        <el-table-column prop="documentType" label="" show-overflow-tooltip>
          <template #default="scope">
            {{ getDocumentType(scope.row.documentType) }}
          </template>
        </el-table-column>
        <el-table-column prop="sequenceNo" label="" show-overflow-tooltip/>
        <el-table-column prop="documentVersion" label="" show-overflow-tooltip/>
        <el-table-column prop="user.nickName" label="" show-overflow-tooltip/>
        <el-table-column prop="substitution.nickName" label="" show-overflow-tooltip/>
        <el-table-column prop="documentPath" label="" :min-width="200" show-overflow-tooltip :ellipsis="true"
                         style="white-space: nowrap; word-break: break-all;"/>

        <el-table-column
            prop="deleted"
            label=""
            align="center"
            sortable="custom"
            show-overflow-tooltip
        >
          <template #default="scope">
            <el-switch
                :model-value="scope.row.deleted"
                :active-value="0"
                :inactive-value="1"
                size="large"
                class="ml-2"
                inline-prompt
                active-text=""
                inactive-text=""
                :before-change="beforeChangeStatus.bind(this, scope.row)"
                style="--el-switch-on-color: #409EFF; --el-switch-off-color: #ff4949"
                v-if="user.role.roleKey === 'regular_user'"
                disabled
            ></el-switch>
            <el-switch
                v-else
                :model-value="scope.row.deleted"
                :active-value="0"
                :inactive-value="1"
                size="large"
                class="ml-2"
                inline-prompt
                active-text=""
                inactive-text=""
                @change="obsoleteChange(scope.row)"
                :before-change="beforeChangeStatus.bind(this, scope.row)"
                style="--el-switch-on-color: #409EFF; --el-switch-off-color: #ff4949"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <el-button :type="scope.row.approvalStatus === 1 ? 'success' : 'danger'"
                       :disabled="true"
                       class="custom-button">
              {{ scope.row.approvalStatus === 1 ? '' : '' }}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <el-button :type="scope.row.publishStatus === 1 ? 'success' : 'danger'"
                       :disabled="true"
                       class="custom-button">
              {{ scope.row.publishStatus === 1 ? '' : '' }}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="" sortable="custom" show-overflow-tooltip/>
        <el-table-column prop="updateTime" label="" sortable="custom"  show-overflow-tooltip/>
        <el-table-column prop="approvalTime" label=""  sortable="custom" show-overflow-tooltip/>
        <el-table-column prop="publishTime" label=""  sortable="custom" show-overflow-tooltip/>
          <el-table-column prop="comment" label=""  sortable="custom" show-overflow-tooltip/>
        <el-table-column fixed="right" label="" width="220">
          <template #default="scope">
            <div class="btn-group">
              <div class="col">
                <el-button size="small" color="#67C23A" style="color: white" @click="previewFile(scope.row)"
                >
                </el-button
                >
                <!--//-->
                <el-button size="small" color="#3BA6C4"  v-if="user.role.roleKey === 'superuser'" @click="updateVersion(scope.row)"
                >
                </el-button
                >
                <el-button size="small" color="#3BA6C4"
                           v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin' || user.role.roleKey === 'designer'"
                           @click="editVersion(scope.row)"
                >
                </el-button
                >
                <el-button size="small" color="#3BA6C4"
                           v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin' || user.role.roleKey === 'designer'"
                           @click="handleGetZipFileList(scope.row)"
                >
                </el-button
                >
                <el-popconfirm title="?"
                               @confirm="handleDelete(scope.row.id,scope.row.documentPath,scope.row.createTime)"
                               v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin' || user.role.roleKey === 'designer'">
                  <template #reference>
                    <el-button color="#f56c6c" style="color: white" size="small" ></el-button>
                  </template>
                </el-popconfirm>
                <el-popconfirm title="?"
                               @confirm="handlePublish(scope.row)"
                               v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin' || user.role.roleKey === 'designer'">
                  <template #reference>
                    <el-button color="#67C23A" style="color: white" size="small" ></el-button>
                  </template>
                </el-popconfirm>
              </div>
              <div class="col">
                <el-button size="small" color="#409EFF" style="color: white" @click="downloadFile(scope.row)"></el-button>
                <el-button size="small" color="#909399" style="color: white" @click="showHistory(scope.row)"></el-button>
                <el-popconfirm title="?"
                               @confirm="handVerify(scope.row)"
                               v-if="user.role.roleKey === 'superuser' || user.role.roleKey === 'department_admin'">
                  <template #reference>
                    <el-button color="#3773EA" style="color: white" size="small" ></el-button>
                  </template>
                </el-popconfirm>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      </el-scrollbar>
    </div>
    <!---->
    <div style="margin: 10px ; padding: 0px">
      <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[5, 10, 20, 40,300,600]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>

    <!--   -->
    <el-dialog v-model="dialogVisible" title="Tips" width="50%" :before-close="handleCloseDialog">
      <el-form :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.itemNo" style="width: 80%">
            <template #suffix>
              <el-icon class="el-input__icon" @click="handleIconClick"><search /></el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.materialNo" style="width: 80%"/>
        </el-form-item>
        <el-form-item label="">
          <div class="radio-group-container">
            <el-radio-group v-model="form.documentType">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
              <el-radio :label="3"></el-radio>
              <el-radio :label="4"></el-radio>
              <el-radio :label="5"></el-radio>
            </el-radio-group>
          </div>
        </el-form-item>
        <el-form-item label="">
          <el-upload ref="upload"
                     :auto-upload="false"
                     :http-request="uploadFile"
                     :disabled="isDisabledUpload"
          >
            <template #trigger>
              <el-button type="primary"></el-button>
            </template>
            <template #tip>
              <div class="el-upload__tip">
               50MB
              </div>
            </template>

          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <div>
            <el-button type="primary" @click="handleBeforeUpload"></el-button>
          </div>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel"></el-button>
        <el-button type="primary" @click="save">
          
        </el-button>
      </span>
      </template>
    </el-dialog>
    <!--   -->
    <el-dialog v-model="dialogVisibleM" title="Tips" width="50%" :before-close="handleCloseMDialog">
      <el-form :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.itemNo" style="width: 80%">
            <template #suffix>
              <el-icon class="el-input__icon" @click="handleIconClick"><search /></el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.materialNo" style="width: 80%"/>
        </el-form-item>
          <el-form-item label="">
            <el-upload ref="upload"
                       class="upload-demo"
                       style="display: inline"
                       :auto-upload="false"
                       :on-change="handleChange"
                       :file-list="fileList"
                       :disabled="isDisabledUploadM"
                       :multiple="true"
                       action="#">
              <template #trigger>
                <el-button type="primary"></el-button>
              </template>
              <template #tip>
                <div class="el-upload__tip">
                 50MB
                </div>
              </template>
            </el-upload>
          </el-form-item>
          <el-form-item>
            <el-button  type="primary" @click="uploadFileM"></el-button>
          </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="closeM">
          
        </el-button>
      </span>
      </template>
    </el-dialog>

    <!--   -->
    <el-dialog v-model="updatedDialogVisibleM" title="" width="50%" :before-close="handleCloseUpdateMDialog">
      <el-form :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.itemNo" style="width: 80%">
            <template #suffix>
              <el-icon class="el-input__icon" @click="handleIconClick"><search /></el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.materialNo" style="width: 80%"/>
        </el-form-item>
        <el-form-item label="">
          <el-upload ref="upload"
                     class="upload-demo"
                     style="display: inline"
                     :auto-upload="false"
                     :on-change="handleChange"
                     :file-list="fileList"
                     :disabled="isDisabledUploadM"
                     action="#">
            <template #trigger>
              <el-button type="primary"></el-button>
            </template>
            <template #tip>
              <div class="el-upload__tip">
               50MB
              </div>
            </template>

          </el-upload>
        </el-form-item>

        <el-form-item>
          <el-button  type="primary" @click="updateFileM"></el-button>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="closeupdatedDialog">
          
        </el-button>
      </span>
      </template>
    </el-dialog>


    <!--   -->
    <el-dialog v-model="updatedDialogVisible" title="Tips" width="50%" :before-close="handleCloseUpdateDialog">
      <el-form :model="form" label-width="120px">
        <el-form-item label="">
          <el-upload ref="updateDocument"
                     :auto-upload="false"
                     :http-request="updateFile">
            <template #trigger>
              <el-button type="primary"></el-button>
            </template>
            <template #tip>
              <div class="el-upload__tip">
               50MB
              </div>
            </template>
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <div>
            <el-button type="primary" @click="handleBeforeUpdate"></el-button>
          </div>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancelUpate"></el-button>
        <el-button type="primary" @click="saveDocument">
          
        </el-button>
      </span>
      </template>
    </el-dialog>

    <!--   -->
    <el-dialog v-model="editDialogVisible" title="" width="50%" :before-close="handleCloseEditMDialog">
      <el-form :model="form" label-width="120px">
        <el-form-item label="">
          <el-upload ref="updateDocument"
                     drag
                     class="upload-demo"
                     style="display: inline"
                     :auto-upload="false"
                     :on-change="handleEditFileChange"
                     :file-list="fileListEdit"
                     :multiple="true"
                     action="#">
            <el-icon class="el-icon--upload" ><upload-filled /></el-icon>
            <div class="el-upload__text" >
               <em></em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                <span style="font-weight: bold; color: lightslategrey; font-family: 'Arial'">50MB</span>
              </div>
            </template>
            <!--<template #trigger>-->
            <!--  <el-button type="primary"></el-button>-->
            <!--</template>-->
            <!--<template #tip>-->
            <!--  <div class="el-upload__tip">-->
            <!--    <span style="font-weight: bold; color: red; font-family: 'Arial'">50MB</span>-->
            <!--  </div>-->
            <!--</template>-->
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <el-button  type="primary" @click="handleEditFile"></el-button>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="closeEditM">
          
        </el-button>
      </span>
      </template>
    </el-dialog>

    <!--   -->
    <el-dialog v-model="deleteFromZipDialogVisible" title="" width="30%" :before-close="closeOneFileDeleteDialog">

      <div class="tree-upload-container">
        <el-space direction="vertical">

          <div class="upload-container">
            <!--//-->
            <el-form :model="form" label-width="150px">
              <el-form-item label="">
                <div class="tree-container">
                  <b></b>
                  <el-tree
                      :data="allFilesPaths"
                      show-checkbox
                      default-expand-all
                      node-key="id"
                      ref="tree"
                      highlight-current
                      @check-change="handleCheckChange"
                      :props="defaultProps">
                  </el-tree>
                </div>
              </el-form-item>
              <el-form-item label="" :class="{ 'upload-disabled': !isTreeChecked }">
                <el-upload ref="upload"
                           drag
                           :auto-upload="false"
                           :http-request="uploadCNFile"
                           :disabled="!isTreeChecked"
                           :limit="1"
                           :on-exceed="handleExceed"
                           @change="handleFileChange"

                >
                  <el-icon class="el-icon--upload" :style="{ color: isTreeChecked ? '' : '#888' }"><upload-filled /></el-icon>
                  <div class="el-upload__text" :style="{ color: isTreeChecked ? '' : '#888' }">
                     <em></em>
                  </div>
                  <template #tip>
                    <div class="el-upload__tip">
                      50MB
                    </div>
                  </template>
                  <!--<template #trigger>-->
                  <!--  <el-button type="primary"  @click="handleUploadButtonClick"></el-button>-->
                  <!--</template>-->
                  <!--<template #tip>-->
                  <!--  <div class="el-upload__tip">-->
                  <!--    50MB-->
                  <!--  </div>-->
                  <!--</template>-->

                </el-upload>

              </el-form-item>
              <el-form-item >
                <div v-if="!isTreeChecked" class="upload-tip">
                  
                </div>
              </el-form-item>
              <el-form-item >
                <div v-if="showUploadTip" class="upload-tip">
                  
                </div>
              </el-form-item>
              <el-form-item label="">
                <div>
                  <el-button type="primary" @click="handleBeforeUpload"></el-button>
                </div>
              </el-form-item>
              <el-form-item >
                <div v-if="showNoUploadCNFile" class="upload-tip">
                  
                </div>
              </el-form-item>
            </el-form>
          </div>
        </el-space>
      </div>
      <template #footer>
      <span class="dialog-footer">
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeOneFileDeleteDialog()"></el-button>
          <el-button type="danger" @click="deleteOneFileFromZip()"></el-button>
        </div>
      </span>
      </template>

    </el-dialog>


    <!---->
    <el-dialog v-model="historyDialogVisible" title="" width="80%" :close-on-click-modal="false"
               :before-close="handleDialogHisClose">
      <el-table
          v-loading="loading"
          :data="historyTableData"
          style="width: 100%"
          border
          class="table"
          :stripe="false"
      >
        <el-table-column type="selection" width="55"/>
        <el-table-column prop="id" label="ID" show-overflow-tooltip/>
        <el-table-column prop="itemNo" label="" show-overflow-tooltip/>
        <el-table-column prop="materialNo" label="" show-overflow-tooltip/>
        <el-table-column prop="documentName" label="" show-overflow-tooltip/>
        <el-table-column prop="documentType" label="" show-overflow-tooltip>
          <template #default="scope">
            {{ getDocumentType(scope.row.documentType) }}
          </template>
        </el-table-column>
        <el-table-column prop="sequenceNo" label="" show-overflow-tooltip/>
        <el-table-column prop="documentVersion" label="" show-overflow-tooltip/>

        <el-table-column
            prop="deleted"
            label=""
            align="center"
            sortable
            show-overflow-tooltip
        >
          <template #default="scope">
            <el-switch
                :model-value="scope.row.deleted"
                :active-value="0"
                :inactive-value="1"
                size="large"
                class="ml-2"
                inline-prompt
                active-text=""
                inactive-text=""
                :before-change="beforeChangeStatus.bind(this, scope.row)"
                style="--el-switch-on-color: #409EFF; --el-switch-off-color: #ff4949"
                disabled
            ></el-switch>
          </template>
        </el-table-column>

        <el-table-column label="">
          <template #default="scope">
            <el-button :type="scope.row.approvalStatus === 1 ? 'success' : 'danger'"
                       :disabled="true"
                       class="custom-button">
              {{ scope.row.approvalStatus === 1 ? '' : '' }}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <el-button :type="scope.row.publishStatus === 1 ? 'success' : 'danger'"
                       :disabled="true"
                       class="custom-button">
              {{ scope.row.publishStatus === 1 ? '' : '' }}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column prop="documentPath" label="" show-overflow-tooltip/>
        <el-table-column prop="user.username" label="" show-overflow-tooltip/>
        <el-table-column prop="substitution.username" label="" show-overflow-tooltip/>
        <el-table-column prop="createTime" label="" show-overflow-tooltip/>
        <el-table-column prop="updateTime" label="" show-overflow-tooltip/>
        <el-table-column prop="approvalTime" label="" sortable  show-overflow-tooltip/>
        <el-table-column prop="publishTime" label="" sortable  show-overflow-tooltip/>
        <el-table-column fixed="right" label="" width="240">
          <template #default="scope">
            <div class="btn-group">
              <div class="col">
                <el-button size="small" @click="previewFile(scope.row)"
                >
                </el-button
                >
                <el-button size="small" @click="downloadFile(scope.row)"></el-button>
              </div>
              <div class="col">
                <el-popconfirm title="?"
                               @confirm="handleDelete(scope.row.id,scope.row.documentPath)"
                               v-if="user.role.roleKey === 'superuser'">
                  <template #reference>
                    <el-button type="danger" size="small" ></el-button>
                  </template>
                </el-popconfirm>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div style="margin: 10px ; padding: 0px">
        <el-pagination
            v-model:current-page="currentHisPage"
            v-model:page-size="pageHisSize"
            :page-sizes="[5, 10, 20, 40]"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalHis"
            @size-change="handleHisSizeChange"
            @current-change="handleHisCurrentChange"
        />
      </div>
    </el-dialog>

    <!--ItemMaster-->
    <el-dialog v-model="itemCodeSelectDialogVisable" title="" width="80%" :close-on-click-modal="false"
               :before-close="handleDialogItemClose">
      <div style="margin: 10px ; padding: 0px">
        <el-form :inline="true" :model="ItemFormInline" class="demo-form-inline" ref="resetformInline">
          <el-form-item label="">
            <el-input v-model="ItemFormInline.code" placeholder="please input" clearable/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="ItemFormInline.name" placeholder="please input" clearable/>
          </el-form-item>
          <el-form-item label="()">
            <el-input v-model="ItemFormInline.specs" placeholder="please input" clearable/>
          </el-form-item>
          <el-form-item>
            <el-button color="#E0BF75" style="margin-left: 5px" :icon="Search" @click="itemMasterLoad"></el-button>
            <el-button color="#E0BF75" style="margin-left: 5px" :icon="RefreshLeft" @click="reset"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-table
          v-loading="loading"
          :data="itemMasterTableData"
          style="width: 100%"
          border
          class="table"
          :stripe="false"
          @row-dblclick="handleRowDoubleClick"
      >
        <el-table-column prop="org" label="ID" sortable show-overflow-tooltip/>
        <el-table-column prop="id" label="ID" show-overflow-tooltip/>
        <el-table-column prop="code" label="" show-overflow-tooltip/>
        <el-table-column prop="specs" label="()" show-overflow-tooltip/>
        <el-table-column prop="name" label="" show-overflow-tooltip/>
        <el-table-column prop="effective" label="" show-overflow-tooltip>
          <template #default="scope">
            {{ getIsEffective(scope.row.effective) }}
          </template>
        </el-table-column>
        <el-table-column prop="mainItemCategoryId" label="ID" show-overflow-tooltip/>
        <el-table-column prop="state" label="">
          <template #default="scope">
            {{ getItemState(scope.row.state) }}
          </template>
        </el-table-column>

        <el-table-column prop="itemFormAttribute" label="" show-overflow-tooltip/>

      </el-table>
      <!---->
      <div style="margin: 10px ; padding: 0px">
        <el-pagination
            v-model:current-page="currentItemPage"
            v-model:page-size="pageItemSize"
            :page-sizes="[5, 10, 20, 40]"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalItem"
            @size-change="handleItemSizeChange"
            @current-change="handleItemCurrentChange"
        />
      </div>
    </el-dialog>

  </div>
</template>
<script setup>
import {RefreshLeft, Search} from '@element-plus/icons-vue';

</script>

<script>

import request from "@/utils/request";
import {ElMessage} from "element-plus";
import { ElNotification } from 'element-plus'
import * as XLSX from 'xlsx';

import * as FileSaver from 'file-saver';
import {Base64} from "js-base64";
import axios from "axios";
import {ref} from "vue";
import JSZip from 'jszip'
import { reactive } from 'vue';
import { h } from 'vue'
import moment from 'moment'
let upload = ref();
let updateDocument = ref();

export default {
  name: 'Document',
  components: {},
  data() {
    return {

      proptype: "",//column.prop


      previewUrl: '',
      form: {
      },
      deleteOneFileform: {
      },
      fileList: [],
      // 
      isCNCorrectSelected:false,
      // 
      isCNSelected:false,

      editFileCNsequenceNo:-1,

      //
      fileListEdit: [],
      editSuccessFlag:true,

      allFilesPaths: [],
      defaultProps: {
        children: 'subFilePath',
        label: 'filepath'
      },
      isTreeChecked:false, // 
      showUploadTip: false, // 
      historyForm: {},
      loading: false,
      previewForm: {},
      //
      createTimeDaterange: '',
      publishTimeDaterange:'',
      shortcuts: [
        {
          text: '',
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24);
            return [start, end];
          },
        },
        {
          text: '',
          value: () => {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 2);
            return [start, end];
          },
        },
        {
          text: '',
          value: () => {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            return [start, end]
          },
        },
        {
          text: '',
          value: () => {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            return [start, end]
          },
        },
        {
          text: '',
          value: () => {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            return [start, end]
          },
        },
      ],
      formInline: {
        sortby:'',
        order:''
      },
      //
      userInputValue:'',

      //
      superDepartInputValue:'',

      //ItemMaster
      ItemFormInline: {},
      search: '',

      // // 
      // file_location:'/files/',

      //
      userId:0,
      user:{
        role:{},
      },



      currentPage: 1,
      pageSize: 10,

      currentHisPage: 1,
      pageHisSize: 10,

      currentItemPage: 1,
      pageItemSize: 10,

      //
      total: 0,

      totalHis: 0,

      totalItem: 0,
      // 
      dialogVisible: false,
      dialogVisibleM: false,
      //
      updatedDialogVisible: false,
      updatedDialogVisibleM: false,
      //
      editDialogVisible: false,
      //
      historyDialogVisible: false,

      //
      deleteFromZipDialogVisible:false,
      deleteOneFileForm: {},

      //
      itemCodeSelectDialogVisable:false,
      fileData: '', // +

      handleDeleleData: '', // 

      EditData: '',// +
      // importData:form,
      // headers:{
      //   token: JSON.parse(sessionStorage.getItem('token'))
      // },
      oldFilePath: '',
      tableData: [],

      historyTableData: [],

      errInsertUpdateFileList:[],

      itemMasterTableData: [],


      // 
      IsUploadCNFileFlag:false,
      showNoUploadCNFile:false,
      ids: [],
      selectedFiles: [],
      // 
      allLoad:false,
      // filesUploadUrl:"http://" + window.server.filesUploadUrl + ":9090/files/uploadDrawingFiles"
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.isTreeReady = true; // true
    });
  },
  created() {

    request.get("/user/getUserInfo",
    ).then(res => {
      this.userId = res.data;
      request.get("/user/" + this.userId
      ).then(res => {
        if (res.code === "0") {
          this.user = res.data;
        }
      })
    });
    // 
    this.allLoad = true;
    this.load()
  },
  computed: {
    isDisabledUpload() {
      const {itemNo, documentType} = this.form || {}
      return !itemNo?.trim() || !documentType
    },
    isDisabledUploadM() {
      const { itemNo } = this.form || {};
      return !itemNo?.trim();
    }

  },
  methods: {

    sortChange(column) {
      //

      console.log("", column.prop, column.order);
      this.currentPage = 1; // 
      if (column.order === 'descending') {
        this.formInline.sortby = column.prop
        this.formInline.order = 'desc'
      } else {
        this.formInline.sortby = column.prop
        this.formInline.order = 'asc'
      }
      this.load();


    },




    handleUserSelect(item){
      console.log("item.value"+item.value)  //  value 
      this.formInline.userId = item.value  //  value 
    },
    //
    handleSuperDepartSelect(item){
      console.log("item.value"+item.value)  //  value 
      this.formInline.departId = item.value  //  value 
    },
    async queryUserSearchAsync(queryString, cb) {
      try {
        console.log("this.userInputValue:" + this.userInputValue)
        request.post("/user/findListByName",JSON.stringify(this.userInputValue)
        ).then(res => {
          console.log(res);
          // 
          this.options = res.data.map(item => ({ value: item.id, label: item.nickName }))
          console.log("this.options:" + JSON.stringify(this.options) )
          cb(this.options)
        })

      } catch (error) {
        console.error(error)
        cb([])
      }
    },
    async querySuperDepartSearchAsync(queryString, cb) {
      try {
        console.log("this.superDepartInputValue:" + this.superDepartInputValue)
        request.post("/depart/findSuperDepartByNameList",JSON.stringify(this.superDepartInputValue)
        ).then(res => {
          console.log(res);
          // 
          this.options = res.data.map(item => ({ value: item.id, label: item.name }))
          console.log("this.options:" + JSON.stringify(this.options) )
          cb(this.options)
        })

      } catch (error) {
        console.error(error)
        cb([])
      }
    },

    async handleDownLoadDocuments() {
      if (!this.ids.length) {
        ElMessage({
          message: '',
          type: 'warning',
        })
        return
      }
      // for (const file of this.selectedFiles) {
      //   const url_raw = window.server.filesUploadUrl + ":" + window.server.filesUploadPort + file.documentPath.replace(window.server.filesUploadUrl,"");
      //   console.log(url_raw)
      //   const url = "http://" + url_raw;
      //   const link = document.createElement('a');
      //   link.href = url;
      //   link.target = "_blank";
      //   link.download = url.split('/').pop(); // 
      //   link.click();
      //   await new Promise(resolve => setTimeout(resolve, 1000)); // 1s
      // }
      const zip = new JSZip()
      for (const file of this.selectedFiles) {
        console.log(file.documentPath.replace(window.server.filesUploadUrl, ''))
        const url_raw = window.server.filesUploadUrl + file.documentPath.replace(window.server.filesUploadUrl, '')
        console.log(url_raw)
        const url = 'http://' + url_raw
        const response = await axios.get(url, { responseType: 'blob' })
        const fileName = url.split('/').pop()
        zip.file(fileName, response.data)
      }

      const zipBlob = await zip.generateAsync({ type: 'blob' })
      const url = window.URL.createObjectURL(zipBlob)
      const link = document.createElement('a')
      link.href = url
      link.target = '_blank'
      link.download = 'drawings.zip' //  drawings.zip
      link.click()
    },
    //
    handleDeleteDocuments() {
      if (!this.ids.length) {
        ElMessage({
          message: '',
          type: 'warning',
        })
        return
      }
      request.post("/document/deleteBatch", this.ids).then(res => {
            console.log(res);
            if (res.code === '0') {
              ElMessage({
                message: res.msg,
                type: 'success',
              })
              let filePaths = res.data;
              console.log("filePaths:" + filePaths)
              this.del_files(filePaths).then(() => {  // load()
                this.load(); // 
              });
            }else if(res.code === '101'){
              ElMessage({
                message: res.msg,
                type: 'warning',
              })
              let filePaths = res.data;
              console.log("filePaths:" + filePaths)
              this.del_files(filePaths).then(() => {  // load()
                this.load(); // 
              })
            } else {
              ElMessage({
                message: res.msg,
                type: 'info',
                //
              })
            }
          }
      )
    },
    handleVerifyDocuments() {
      if (!this.ids.length) {
        ElMessage({
          message: '',
          type: 'warning',
        })
        return
      }
      request.post("/document/verifyBatch", this.ids).then(res => {
            console.log(res);
            if (res.code === '0') {
              ElMessage({
                message: res.msg,
                type: '',
              })
              this.load();
            }else {
              ElMessage({
                message: res.msg,
                type: 'warning',
                //
              })
              this.load()
            }
          }
      )
    },
    //
    handleSelectionChange(val) {
      this.ids = val.map(v => v.id)
      this.selectedFiles = val
      console.log("this.selectedFiles:" + this.selectedFiles)
    },

    handleIconClick() {
      console.log('Search icon clicked!');
      this.itemMasterLoad();
      this.itemCodeSelectDialogVisable = true;

    },

    //
    handleRowDoubleClick(row) {
      // 
      console.log('', row);
      this.form.itemNo = row['specs'];
      this.form.materialNo = row['code'];
      this.handleDialogItemClose();

    },
    async itemMasterLoad() {
      try {

        this.loading = true; // Loading

        //
        await this.delay(1000);

        request.post(`/itemMaster/${this.currentItemPage}/${this.pageItemSize}`,JSON.parse(JSON.stringify(this.ItemFormInline))
        ).then(res => {
          console.log(res);
          this.itemMasterTableData = res.data.records;
          this.totalItem = res.data.total;
          this.$nextTick(() => {
            // Loading
            this.loading = false;
          });

        })
      } catch (error) {
        console.error(error)
      }
    },
    async historyLoad() {
      try {
        console.log("loadingState:"+this.loading)
        this.loading = true; // Loading

        //
        await this.delay(1000);

        request.post(`/document/historyList/${this.currentHisPage}/${this.pageHisSize}`, this.historyForm
        ).then(res => {
          console.log(res);
          this.historyTableData = res.data.records;
          this.totalHis = res.data.total;


        })
      } catch (error) {
        console.error(error)
      } finally {
        this.loading = false; // Loading
      }
    },
    //
    showHistory(row) {
      this.historyForm = JSON.parse(JSON.stringify(row));
      this.historyLoad();
      this.historyDialogVisible = true;
    },
    //
    downloadFile(row) {
      const url_raw = window.server.filesUploadUrl + row.documentPath.replace(window.server.filesUploadUrl,"");
      console.log(url_raw)
      const url = "http://" + url_raw;
      const link = document.createElement('a');
      link.href = url;
      link.download = url.split('/').pop(); // 
      link.click();
    },
    //
    updateVersion(row) {
      this.form = JSON.parse(JSON.stringify(row));
      try {
        request.post("/document/updateconfirm",this.form
        ).then(res => {
          console.log(res);
          if (res.code === '0') {
            ElMessage({
              message: '',
              type: 'success',
            })
            this.oldFilePath = this.form.documentPath;
            this.updatedDialogVisible = true;
            this.$nextTick(() => {
              this.$refs["updateDocument"].clearFiles();
            })
          }else{

              ElMessage({
                message: res.msg,
                type: 'error',

              })
          }


        })
      } catch (error) {
      console.error(error)
    }
    },
    //
    async editVersion(row) {

      this.form = JSON.parse(JSON.stringify(row));
      // console.log("document:" + string);
      try {
        const res =  await request.post("/document/publishconfirm",this.form)
        if (res.code === '0') {
          try {
            this.editDialogVisible = true;
            this.$nextTick(() => {
              this.$refs["updateDocument"].clearFiles();
            })
          }catch (error){
            console.error(error)
          }
        }else{
          ElMessage({
            message: res.msg,
            type: 'error',

          })
        }
      }catch (error){
        console.error(error)
      }





    },
    //
    async handleGetZipFileList(row){

      this.form = JSON.parse(JSON.stringify(row));
      let handleDeleleData = new FormData();
      let string = JSON.stringify(this.form);
      handleDeleleData.append('document', string);
      // console.log("document:" + string);
      try {
        const res =  await request.post("/document/publishconfirm",this.form)
        if (res.code === '0') {
            try {
              const response = await axios.request({
                method: 'post',
                url: 'http://' + window.server.filesUploadUrl + ':9090/files/getDocumentList',
                headers: {
                  'Content-Type': 'multipart/form-data',
                  'token': JSON.parse(sessionStorage.getItem('token'))
                },
                data: handleDeleleData
              });
              if (response.data.code === '0') {
                this.allFilesPaths = response.data.data
                //
                this.showUploadTip = false;
                // this.allFilesPaths=response.data;
                this.deleteFromZipDialogVisible = true;
                this.$nextTick(() => {
                  this.$refs["upload"].clearFiles();
                })
                console.log("this.allFilesPaths:"+this.allFilesPaths);
              }else{
                ElMessage({
                  message: response.data.msg,
                  type: 'warning',
                })
              }
            }catch (error){
              console.error(error)
            }
          }else{
            ElMessage({
              message: res.msg,
              type: 'error',

            })
          }
        }catch (error){
        console.error(error)
      }

    },
    handleCheckChange() {
      const checkedKeys = this.$refs.tree.getCheckedKeys();
      this.isTreeChecked = checkedKeys.length > 0;
    },
    handleUploadButtonClick(fileList) {
      this.showUploadTip = !this.isTreeChecked; // 
    },
    //
    async deleteOneFileFromZip(){
      if (!this.isTreeChecked) {
        this.showUploadTip = true; // 
        return;
      }
      //CN 
      if (!this.IsUploadCNFileFlag) {
        this.showNoUploadCNFile = true; // 
        return;
      }

      //
      // 
      //A01 4
      console.log("form"+JSON.stringify(this.form))
      let form1 = Object.assign({}, this.form);
      form1.documentVersion = "A01";
      //form
      form1.documentType = 4;
      form1.sequenceNo = this.deleteOneFileForm.sequenceNo;
      // this.oldFilePath
      form1.documentPath = this.oldFilePath;
      //formid
      let res1 = await request.post("/document/insert", form1);
      console.log("");
      if (res1.code === '0') {

        console.log("")
        ElMessage({
          message: "",
          type: 'success',
        })
        // 
        const checkedNodes = this.$refs.tree.getCheckedNodes(); // 
        const filePaths = checkedNodes.map(node => node.filepath); //  filepath 
        console.log(filePaths); //  filepath 
        //
        let fileListStr=""
        for (let i=0;i<filePaths.length;i++){
          fileListStr+=filePaths[i]+",";
        }
        fileListStr.substr(0,fileListStr.length-1);
        console.log("fileListStr:" + fileListStr)
        let handleDeleleData = new FormData();
        //this.form
        let string = JSON.stringify(this.form);
        console.log("form"+string)
        handleDeleleData.append('document', string);
        handleDeleleData.append('fileListStr', fileListStr);
        console.log("document:" + string);
        // 
        const response = await axios.request({
          method: 'post',
          url: 'http://' + window.server.filesUploadUrl + ':9090/files/deleteOneFileFromZip',
          headers: {
            'Content-Type': 'multipart/form-data',
            'token': JSON.parse(sessionStorage.getItem('token'))
          },
          data: handleDeleleData
        });
        //
        if (response.data.code === '0') {

          // 
          //formform
          this.form.documentPath = response.data.data.filePath;
          this.form.documentVersion = response.data.data.newVersion;


          //  
          this.form.id =
          //   id  
          console.log("this.form:" +  JSON.stringify(this.form))
          //id idid
          let res = await request.post("/document/updateM", this.form);
          console.log("");
          if (res.code === '0') {
            ElMessage({
              message: "",
              type: 'success',
            })

          } else {

            ElMessage({
              message: " ",
              type: 'error',
            })
            console.log("this.form" + this.form);
            // 
            console.log(this.oldFilePath)
            this.del_file(this.form.documentPath)
          }

        }else{
          //  
          ElMessage({
            message: " ",
            type: 'error',
          })
        }

      } else {
        console.log("")
        ElMessage({
          message: "",
          type: 'error',
        })
        console.log("this.form" + this.form);
        //    
        this.del_file(this.form.documentPath)
      }


      this.deleteFromZipDialogVisible=false;
      this.load();
    },

    closeOneFileDeleteDialog(){
      this.deleteFromZipDialogVisible  = false;//
      this.load();
    },
    handleExceed(files, uploadFiles) {
      ElMessage({
        message: `1 ${files.length}  ${files.length + uploadFiles.length} `,
        type: 'error',
      })
      this.$message.warning(``);
    },
    async handleFileChange(file) {
      // 
      const selectedFile = file.raw;
      // 
      const fileName = selectedFile.name;
      console.log(fileName)

      this.deleteOneFileForm = Object.assign({}, this.form);

      // 
      if (fileName.indexOf('') !== -1 || fileName.indexOf('') !== -1) {
        this.deleteOneFileForm.documentType = 4;
        // 
        const regex = /_(\d+)\./;
        const matches = fileName.match(regex);
        if (matches && matches.length > 1) {
          //  this.form.documentType  this.form.sequenceNo 
          this.deleteOneFileForm.sequenceNo = parseInt(matches[1]);

          //formA01 insert
          this.deleteOneFileForm.documentVersion = "A01";
          let res = await request.post("/document/confirm", this.deleteOneFileForm);

          if (res.code === '0') {
            console.log("");

          } else {
            this.isCNSelected = true;
            console.log("");
            // 
            ElMessage({
              message: '',
              type: 'error',
            })
            // ""
            this.$refs.upload.clearFiles();
          }

        }else{
          console.log("");
          ElMessage({
            message: '',
            type: 'error',
          })
          // ""
          this.$refs.upload.clearFiles();
        }
      }else{
        console.log("");
        ElMessage({
          message: '',
          type: 'error',
        })
        // ""
        this.$refs.upload.clearFiles();
      }


      // ...
    },
    //     
    uploadCNFile(params) {
      let form1 = reactive({ ...this.form });
      form1.documentType = 4;
      console.log("form"+JSON.stringify(this.form))
      this.fileData = new FormData()
      let string = JSON.stringify(form1)
      this.fileData.append('document', string)
      this.fileData.append("file", params.file) // append
      axios.request({
        method: 'post',
        url: 'http://' + window.server.filesUploadUrl + ':9090/files/uploadDocumentFiles',
        headers: {
          'Content-Type': 'multipart/form-data',
          'token': JSON.parse(sessionStorage.getItem('token'))
        },
        data: this.fileData
      }).then(response => {
        let res = response.data;
        if (res.code === '0') {
          ElMessage({
            message: '',
            type: 'success',
          })
          // A01
          this.form.documentVersion = "A01";
          this.form.documentPath = res.data;
          this.oldFilePath = this.form.documentPath;
        }
      }).catch(() => {
      })
    },

    handleDialogHisClose() {
      this.historyTableData = [];
      this.historyDialogVisible = false;
    },
    handleDialogItemClose(){
      this.itemMasterTableData = [];
      this.itemCodeSelectDialogVisable = false;
    },
    handleCloseUpdateDialog() {
      //  cancel 
      this.cancelUpate();
    },
    //cancel 
    handleCloseEditDialog() {
      //  cancel 
      this.cancel();
    },
