    beforeChangeStatus(row) {
      this.form = JSON.parse(JSON.stringify(row));
      return new Promise((resolve, reject) => {
        try {
          request.post("/document/updateconfirm",this.form
          ).then(res => {

            if (res.code === '0') {
              //
              console.log("")
              if(row.deleted == 0){
                row.deleted = 1
              }else{
                row.deleted = 0
              }


              resolve(true)
            }else{

              ElMessage({
                message: res.msg,
                type: 'error',

              })
              reject(false)
            }


          })
        }catch (error) {
          reject(false)
        }
      })
