    exportExcel() {
      // 
      const mapping = {
        id: '',
        itemNo: '',
        materialNo: '',
        documentName: '',
        documentType: '',
        sequenceNo: '',
        documentVersion: '',
        documentPath: '',
        createTime: '',
        updateTime: ''
      };
      // 
      const filteredData = this.tableData.map(item => {
        const newItem = {};
        for (const key in item) {
          if (Object.prototype.hasOwnProperty.call(item, key) && mapping[key]) {
            newItem[key] = item[key];
          }
        }
        return newItem;
      });
      // 
      const data = filteredData.map(item => {
        const newItem = {};
        for (const key in item) {
          if (Object.prototype.hasOwnProperty.call(item, key)) {
            newItem[mapping[key] || key] = item[key];
          }
        }
        //  documentType 
        if (newItem['']) {
          switch (newItem['']) {
            case 1:
              newItem[''] = '';
              break;
            case 2:
              newItem[''] = '';
              break;
            case 3:
              newItem[''] = '';
              break;
            case 4:
              newItem[''] = '';
              break;
            case 5:
              newItem[''] = '';
              break;
            default:
              break;
          }
        }
        return newItem;
      });
      // Excel
      const ws = XLSX.utils.json_to_sheet(data);

      ws['!cols'] = [
        { wpx: 40},  //  100px
        { wpx: 100},  //  200px
        { wpx: 80 },  //  200px
        { wpx: 200 },
        { wpx: 60 },
        { wpx: 60},
        { wpx: 60},
        { wpx: 200 },
        { wpx: 120 },
        { wpx: 80 },
      ];
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
      // 
      const fileName = '.xlsx';
      const blob = new Blob([XLSX.write(wb, {bookType: 'xlsx', type: 'array'})], {type: 'application/octet-stream'});
      FileSaver.saveAs(blob, fileName);
    },
    getDocumentType(type) {
      switch (type) {
        case 1:
          return ''
        case 2:
          return ''
        case 3:
          return ' '
        case 4:
          return ''
        case 5:
          return ''
        default:
          return ''
      }
    },
    getIsEffective(type) {
      switch (type) {
        case 0:
          return ''
        case 1:
          return ''
        default:
          return ''
      }
    },
    getItemState(type) {
      switch (type) {
        case 0:
          return ''
        case 1:
          return ''
        case 2:
          return ''
        default:
          return ''
      }
    },
    filesUploadSuccess(res) {
      this.form.documentPath = res.data;
    },
    delay(time) {
      return new Promise(resolve => setTimeout(resolve, time));
    },
    async load() {

        if(this.allLoad){
          this.reset();//
        }
        console.log("this.createTimeDaterange" + this.createTimeDaterange);
        const [start, end] = this.createTimeDaterange? this.createTimeDaterange : [null, null]; // 
        if (start && end) {
          this.formInline.createTimeStart = moment(start).format('YYYY-MM-DD HH:mm:ss')
          this.formInline.createTimeEnd = moment(end).format('YYYY-MM-DD HH:mm:ss')
        }

        console.log("this.publishTimeDaterange" + this.publishTimeDaterange);
        const [start1, end1] = this.publishTimeDaterange? this.publishTimeDaterange : [null, null]; // 
        if (start1 && end1) {
          this.formInline.publishTimeStart = moment(start1).format('YYYY-MM-DD HH:mm:ss')
          this.formInline.publishTimeEnd = moment(end1).format('YYYY-MM-DD HH:mm:ss')
        }

        console.log('this.formInline',JSON.stringify(this.formInline))
        try {

          this.loading = true; // Loading
          //
          await this.delay(1000);

          request.post(`/document/${this.currentPage}/${this.pageSize}`, JSON.parse(JSON.stringify(this.formInline))
          ).then(res => {
            console.log(res);
            this.tableData = res.data.records;
            this.total = res.data.total;
            // 
            this.formInline.sortby = ''
            this.formInline.order = ''


          })
        } catch (error) {
          // 
          this.formInline.sortby = ''
          this.formInline.order = ''
          console.error(error)
        } finally {
          this.loading = false; // Loading
          // 
          this.formInline.sortby = ''
          this.formInline.order = ''
          this.allLoad = false
        }



    },

    reset() {
      this.formInline = {};
      this.ItemFormInline = {};
      this.createTimeDaterange = '';
      this.publishTimeDaterange = '';
      this.$nextTick(()=>{
        this.$refs["resetformInline"].resetFields();
      })

    },
    add() {

      this.dialogVisible = true;
      this.form = {};

      this.$nextTick(() => {
        this.$refs["upload"].clearFiles();
      })
    },
    add1() {
      console.log("1")
      this.dialogVisibleM = true;
      this.form = {};

      this.$nextTick(() => {
        this.$refs["upload"].clearFiles();
      })
    },
    //
    updateM(){
      console.log("")
      this.updatedDialogVisibleM = true;
      this.form = {};
      this.fileList =[];
      this.$nextTick(() => {
        this.$refs["upload"].clearFiles();
      })
    },
    //
    del_file(path) {
      console.log("path" + path)
      request.post("/files/delDocumentFile", {delDocumentPath: path}).then(res => {
            console.log(res);
            if (res.code === '0') {
              // ElMessage({
              //   message: '',
              //   type: 'info',
              // })
              ElNotification({
                title: 'Info',
                message: "",
                duration: 3000,
                type: 'info',
              })
            } else {
              ElMessage({
                message: '',
                type: 'error',
                //
              })
            }
          }
      )
    },
    //
    del_files(filePaths) {
      return request.post("/files/delDocumentFiles", filePaths).then(res => {
            console.log(res);
            if (res.code === '0') {
              ElMessage({
                message: 'nginx',
                type: 'info',
              })
            } else {
              ElMessage({
                message: 'nginx',
                type: 'error',
                //
              })
            }
          }
      )
    },


    saveDocument() {
      // Convert documentType to an integer before submitting the form
      this.form.documentType = parseInt(this.form.documentType);
      //
      if (this.form.id) {

        const fieldNames = {
          itemNo: '',
          documentType: '',
          // 
          documentPath: '',
        };
        const requiredFields = Object.keys(fieldNames);
        let isFormValid = true;
        let missingFields = [];
        for (let i = 0; i < requiredFields.length; i++) {
          const field = requiredFields[i];
          if (!this.form[field]) {
            isFormValid = false;
            missingFields.push(fieldNames[field]);
          }
        }
        if (isFormValid) {
          request.post("/document/editDocument", this.form).then(res => {

            if (res.code === '0') {
              ElMessage({
                message: '',
                type: 'success',
              })
              this.load();//
            } else {
              ElMessage({
                message: res.msg,
                type: 'error',
                //
              })
            }
          });
        } else {
          ElMessage({
            message: `${missingFields.join(', ')}`,
            type: 'warning',
          })
        }
      }
      this.updatedDialogVisible = false;//
    },

    editDocument() {
      //
      if (this.form.id) {
        if (isFormValid) {
          request.post("/editdocument", this.form).then(res => {

            if (res.code === '0') {
              ElMessage({
                message: '',
                type: 'success',
              })
              this.load();//
            } else {
              ElMessage({
                message: res.msg,
                type: 'error',
                //
              })
            }
          });
        } else {
          ElMessage({
            message: `${missingFields.join(', ')}`,
            type: 'warning',
          })
        }
      }
      this.updatedDialogVisible = false;//
    },


    save() {
      // Convert documentType to an integer before submitting the form
      this.form.documentType = parseInt(this.form.documentType);
      //idid
      this.form.userId =  this.userId;
      //
      const fieldNames = {
        itemNo: '',
        documentType: '',
        documentPath: '',
      };
      const requiredFields = Object.keys(fieldNames);
      let isFormValid = true;
      let missingFields = [];
      for (let i = 0; i < requiredFields.length; i++) {
        const field = requiredFields[i];
        if (!this.form[field]) {
          isFormValid = false;
          missingFields.push(fieldNames[field]);
        }
      }
      if (isFormValid) {
        //
        request.post("/document", this.form).then(res => {
          console.log("");
          if (res.code === '0') {
            ElMessage({
              message: '',
              type: 'success',
            })
            this.load();//
          } else {
            console.log("this.form" + this.form);
            ElMessage({
              message: res.msg,
              type: 'error',

            })
            //    
            console.log(this.oldFilePath)
            this.del_file(this.form.documentPath)
          }
        });
      } else {
        ElMessage({
          message: `${missingFields.join(', ')}`,
          type: 'warning',
        })
      }
      this.dialogVisible = false;//


    },
    closeM(){
      this.dialogVisibleM = false;//
      this.fileList =[];
      this.load();
    },
    closeEditM(){
      this.editDialogVisible = false;//
      this.fileListEdit =[];
      this.load();
    },

    closeupdatedDialog(){
      this.updatedDialogVisibleM = false;//
      this.fileList =[];
      this.load();
    },

    cancelUpate() {
      //  
      if (this.form.documentPath !== this.oldFilePath) {
        console.log("")
        console.log("this.form.documentPath:" + this.form.documentPath)
        // this.del_file(this.form.documentPath.substring(this.form.documentPath.indexOf("/files/") + "/files/".length))
        // this.del_file(this.form.documentPath.substring(this.form.documentPath.indexOf(this.file_location) + this.file_location.length))
        this.del_file(this.form.documentPath)
      }
      //
      //
      this.updatedDialogVisible = false;
    },
    //
    cancel() {
      //   
      if (this.oldFilePath !== "") {
        // 
        console.log("this.oldFilePath:" + this.oldFilePath)
        this.del_file(this.oldFilePath)
      }

      //
      this.dialogVisible = false;
    },
    previewFile(row) {
      this.form = JSON.parse(JSON.stringify(row));

      request.get("/document/getId/" + this.form.id).then(res => {
        console.log(res);
        if (res.code === '0') {

          // 
          this.previewUrl = "http://" + window.server.filesUploadUrl
              + res.data.documentPath.replace(window.server.filesUploadUrl,"");;
          console.log("this.previewUrl" + this.previewUrl);

          window.open('http://' + window.server.filesUploadUrl  + ':'
              + window.server.kkfileviewPort +'/onlinePreview?url='
              + encodeURIComponent(Base64.encode(this.previewUrl)));
        } else {
          ElMessage({
            message: '',
            type: 'error',
          })
        }
      });

    },
    handleSizeChange() {
      this.load()
    },

    handleCurrentChange() {
      this.load()
    },
    handleHisSizeChange() {
      this.historyLoad()
    },
    handleHisCurrentChange() {
      this.historyLoad()
    },

    handleItemSizeChange() {
      this.itemMasterLoad()
    },
    handleItemCurrentChange() {
      this.itemMasterLoad()
    },

    handleDelete(id, documentPath,createTime) {
      console.log(id)
      console.log("documentPath=" + documentPath)
      console.log("createTime=" + createTime)
      request.delete(`/document/${id}/${createTime}`).then(res => {
        console.log(res);
        if (res.code === '0') {
          ElMessage({
            message: '',
            type: 'success',
          })
          this.del_file(documentPath)
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
        this.load();
      });
    },
    handlePublish(row){

      this.form = JSON.parse(JSON.stringify(row));
      console.log("edit.row:", this.form)
      // this.oldFilePath =  this.form.drawingPath;
      request.post(`/document/publish`,this.form).then(res => {
        console.log(res);
        if (res.code === '0') {
          ElMessage({
            message: '',
            type: 'success',
          })
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
        this.load();
      });
    },
    handVerify(row) {
      this.form = JSON.parse(JSON.stringify(row));
      console.log("edit.row:", this.form)
      // this.oldFilePath =  this.form.drawingPath;
      request.post(`/document/verifyPass`,this.form).then(res => {
        console.log(res);
        if (res.code === '0') {
          ElMessage({
            message: '',
            type: 'success',
          })
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
        this.load();
      });
    },

  }
}
</script>
